@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,300;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Mono&display=swap);
/* Pro- stands for Project btw :D */

.pro-btn {
    font-size: 16px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 11px;
    padding-left: 11px;
    border-color: #00000000;
    color: white;
    width: auto;
    min-width: 100px;
    max-width: 190px;
    background-color:#1E1E1E;
    border-radius: 5px;
    cursor: pointer;
}

.pro-btn:hover {
    background-color:#2e2e2e;
}

.pro-btn-coming-soon {
    font-family: 'DM Mono', monospace;
    font-size: 16px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 34px;
    padding-left: 34px;
    border-color: #00000000;
    color: white;
    width: auto;
    min-width: 200px;
    max-width: 300px;
    background-color:#1E1E1E;
    border-radius: 5px;
    cursor: pointer;
}

.pro-btn-coming-soon:hover {
    background-color:#2e2e2e;
}

.pro-card-dark {
    text-decoration: none;
    border-radius: 7px;
    background-color: #1E1E1E;
    width: auto;
    height: auto;
    border-width: 0px;
    box-shadow: black;
    display: flex;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
}

.pro-card-dark:hover {
    background-color: #212121;
}

.pro-card {
    font-family: 'DM Mono', monospace;
    /* max-width: 400px; */
    margin: auto;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
body { 
    background-color: #121212;
}
:root {
    --text-color: #00aa00;
  }
  body {
    font-family: 'Roboto Mono', sans-serif;
    font-size: 18px;
    font-weight: 500;
    color: #FCFCFC;
    line-height: 1.6;
  }
  
  .wb-body {
    font-weight: 600;
    background: #111;
    padding: 20px;
  }
  .hidden {
    display: none;
  }
  .cursor {
    font-weight: 700;
    -webkit-animation: 1s blink step-end infinite;
            animation: 1s blink step-end infinite;
  }
  @-webkit-keyframes blink {
    from,
    to {
      color: transparent;
    }
  
    50% {
      color: #00aa00;
      color: var(--text-color);
    }
  }
  @keyframes blink {
    from,
    to {
      color: transparent;
    }
  
    50% {
      color: #00aa00;
      color: var(--text-color);
    }
  }

/* Navbar */

  .nav-card {
    width: auto;
    margin: auto;
    margin-top: 8px;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: center;
}

  .nav-btn {
    font-size: 16px;
    margin: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    border-color: #00000000;
    color: #00AA00;
    width: auto;
    background-color:#1E1E1E;
    border-radius: 5px;
    cursor: pointer;
}

.nav-btn:hover {
    background-color:#2e2e2e;
}

.nav-theme {
  text-align: center; 
  font-weight: 500;
  font-family: monospace;
}

/* About btn */

.aboutButton {
  justify-content:flex-start;
  align-items:flex-start;
}

/* Contact btn */

.contactButton {
  justify-content:flex-end;
  align-items:flex-end;
}
